import { Component, OnInit, ViewChild } from '@angular/core';
import {QueryService} from '../query.service'
import { Router } from '@angular/router';
import {DataTransferService} from '../data-transfer.service';


@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss']
})
export class ProductsComponent implements OnInit {
  public data:any
  mydata:Array<Object>
  cart:Array<any>
  constructor(private q:QueryService, 
    public router:Router, 
    private product:DataTransferService, 
    private product2:DataTransferService) 
    
    { this.getProducts(); this.cart=[]; }

  getProducts():void {
    let path:string = './assets/products.json'
    this.q.getData(path).subscribe(
      res => { this.data = res, 
        console.log(this.data)},
      err => { console.log (err)}
    )
  }
  redirectTo(proname:string):void {
    this.router.navigate(['/details/'],{queryParams:{proname}})
  }

  sendData(hamada){
    this.product.dataTransporter(hamada)
  }

  sendToCart(pro){
    this.cart.push(pro)
    this.product2.dataTransporter2(this.cart)
  }

  Gridview(){
    let x=document.querySelectorAll('.all')
    let w = document.querySelectorAll('.image')
    let card = document.querySelectorAll('.card-body');
    let magic = document.querySelectorAll('.magic');
    let main = document.querySelectorAll('.main');
    for(var i=0; i<x.length;i++){
      main[i].classList.add('col-md-4');
      x[i].classList.remove('col-md-12');
      magic[i].classList.remove('row');
      w[i].classList.remove('col-md-4');
      card[i].classList.remove('col-md-8');
    }
  }

  Listview():void{
    let x=document.querySelectorAll('.all')
    let w = document.querySelectorAll('.image')
    let card = document.querySelectorAll('.card-body');
    let magic = document.querySelectorAll('.magic');
    let main = document.querySelectorAll('.main');
    for(var i=0; i<x.length;i++){
      main[i].classList.remove('col-md-4');
      x[i].classList.add('col-md-12');
      magic[i].classList.add('row');
      w[i].classList.add('col-md-4');
      card[i].classList.add('col-md-8');
    }
    
  }

  
  ngOnInit() {
   
  }

}
