import { Component, OnInit, ViewChild } from '@angular/core';
import {DataTransferService} from '../data-transfer.service';
import { NgModel, NgForm} from '@angular/forms';
import {ElementRef} from '@angular/core';


@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {
  inf: Array<object> =[]
  
  @ViewChild('message') m:ElementRef;
  @ViewChild('myinput') i:ElementRef
  Qty = '1';
  constructor(private data:DataTransferService) { 
    // this.inf = this.inf2;
  }

  deleteProduct(i){
    this.inf.splice(i,1)
  }

  checkvalue(Quantity){
    if(Quantity.value < 1 ){
      this.m.nativeElement.setAttribute("style","display:block")
    }    
    else if(Quantity.value >= 1){
       this.m.nativeElement.setAttribute("style","display:none")
    }
  }

  ngOnInit() {
    this.data.dataContainer2.subscribe(mydata=> {this.inf=mydata,console.log(mydata)})
    // console.log(this.inf);
    }
 
}
