import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataTransferService {

  private data = new BehaviorSubject([])
  dataContainer = this.data.asObservable();

  private data2= new BehaviorSubject([])
  dataContainer2= this.data2.asObservable();
  constructor() { }

  dataTransporter(getdata){
    this.data.next(getdata)
  }
  dataTransporter2(getdata2){
    this.data2.next(getdata2)
  }
}
